import requests from './request'

//首页轮播图数据
export const reqCarousel = () => {
    return requests({
        url:'/banner?typr=0'
    })
}
//热门推荐标签
export const hot = () => {
    return requests({
        url:'/playlist/hot'
    })
}

//热门标签数据
export const reqPlayListHot = (cat = "") => {
    return requests({
        url:`/top/playlist?limit=6&order=hot&cat=${cat}&offset=0`
    })
}
//新碟上架数据
export const reqDiscList = (area) => {
    return requests({
        url:`/top/album?limit=12&offset=0&area=${area}&type=new&year=&month=`
    })
}
//首页排行版数据
export const reqHomeRankcList = (id) => {
    return requests({
        url:`/playlist/detail?id=${id}&s=8`
    })
}
//首页最新MV数据
export const reqNewMvList = (area = '') => {
    return requests({
        url:`/mv/all?area=${area}&limit=12`
    })
}
//首页最新MV数据
export const reqradioList = () => {
    return requests({
        url:'/dj/hot?limit=6&offset=0'
    })
}
export const reqHotsingerList = () => {
    return requests({
        url:'/top/artists?offset=0&limit=18'
    })
}
