import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/reset.css'
import { ElButton, ElInput,ElCarousel,ElCarouselItem,ElContainer, ElAside, ElHeader,ElMain ,ElTabs,ElTabPane, ElImage, ElTag,ElTooltip } from 'element-plus'
import 'element-plus/dist/index.css'
createApp(App)
        .use(ElButton)
        .use(ElInput)
        .use(ElCarousel)
        .use(ElCarouselItem)
        .use(ElContainer)
        .use(ElAside)
        .use(ElHeader)
        .use(ElMain)
        .use(ElTabs)
        .use(ElImage)
        .use(ElTag)
        .use(ElTabPane)
        .use(ElTooltip)
        .use(store).use(router).mount('#app')
